<template>
  <div class="appstore">
    <header-view :headerObj="{title:title}"></header-view>
    <component :is="currentView"></component>
    <div class="footer">
      <div :class="{active:recomIsActive}"  class="footerNav" @click="pageChange('RecommendNice','')">
        <i class="icon-comment-alt"></i>
        <span></span>
      </div>
      <div :class="{active:appliIsActive}" class="footerNav" @click="pageChange('ApplicationList','')">
        <i class="icon-user"></i>
        <span></span>
      </div>
      <div :class="{active:compIsActive}" class="footerNav" @click="pageChange('CompanyMade','')">
        <i class="icon-group"></i>
        <span></span>
      </div>
    </div>
  </div>
</template>

<script>
    import router from '../../router/index.js'
    import HeaderView from '../../components/appStore/header-x'
    import RecommendNice from './recommentNice'
    import ApplicationList from './application'
    import CompanyMade from './companyMade'
    export default {
        name: "appstore",
        data:function(){
            return {
                title: "",
                currentView:RecommendNice,
                recomIsActive:true,
                appliIsActive:false,
                compIsActive:false
            }
        },
        components:{
          HeaderView,
          RecommendNice,
          ApplicationList,
          CompanyMade
        },
        methods:{
          pageChange:function(url,title){
            this.currentView=url;
            this.title=title;
            this.compIsActive=false;
            this.recomIsActive=false;
            this.appliIsActive=false;
            switch(url){
              case "RecommendNice":this.recomIsActive=true;break;
              case "ApplicationList":this.appliIsActive=true;break;
              case "CompanyMade":this.compIsActive=true;break;
          }
        }
      }
    }

</script>

<style lang='less' scoped>
@import '../../public/comment.less';
  div.appstore{
    padding: 2rem 0 2.5rem 0;
    .footer{
      position:fixed;
      left:0;
      bottom:0;
      z-index: 100;
      display: flex;
      display: -webkit-flex;
      border-top: 1px @gray solid;
      width:100%;
      background: @white;
      .footerNav{
        flex: 1;
        text-align: center;
        padding:.1rem 0;
        >i{
          font-size: 20px;
          padding-top:.25rem;
        }
        >span{
          display: block;
          font-size: 12px;
          margin-top: .1rem;
        }
      }
      .footerNav.active{
        i,span{color:red;}
      }
    }
  }

</style>



