<template>
    <div class="application">
        <warp-slider :sliderUrl="{sliderUrlObj:obj}"></warp-slider>
        <div class="hot">
          <h4><span @click="jump('/applist','','./static/img/slider1.png')"></span></h4>
          <div>
            <div>
            <ul ref="hot">
              <template  v-for="item in hotItems">
                <li @click="jump('/appDetail',123)">
                  <img :src="item.imgSrc" alt="">
                  <p>{{item.title}}</p>
                </li>
              </template>
            </ul>
            </div>
          </div>
        </div>
        <div class="company">
          <h4> <span @click="jump('/applist','','./static/img/slider2.jpg')"></span></h4>
          <div>
            <div>
            <ul ref="company">
              <template  v-for="item in companyItems">
                <li @click="jump('/appDetail',123)">
                  <img :src="item.imgSrc" alt="">
                  <p>{{item.title}}</p>
                </li>
              </template>
            </ul>
          </div>
          </div>
        </div>
        <div class="service">
          <h4> <span @click="jump('/applist','','./static/img/slider3.jpg')"></span></h4>
          <div>
            <div>
            <ul ref="service">
              <template  v-for="item in serviceItems">
                <li @click="jump('/appDetail',123)">
                  <img :src="item.imgSrc" alt="">
                  <p>{{item.title}}</p>
                </li>
              </template>
            </ul>
            </div>
          </div>
        </div>
        <p @click="jump('/applist','')"></p>
    </div>
</template>

<script>
   import WarpSlider from '../../components/appStore/slider'
   import router from '../../router'
    export default {
        name: "application",
        data:function() {
            return {
                msg: "hello application",
                hotItems:[
                  {
                    imgSrc:"./static/img/hotRecomm.png",
                    title:'ERP'
                  },{
                    imgSrc:"./static/img/hotRecomm.png",
                    title:''
                  },{
                    imgSrc:"./static/img/hotRecomm.png",
                    title:'ERP'
                  },{
                    imgSrc:"./static/img/hotRecomm.png",
                    title:''
                  },{
                    imgSrc:"./static/img/hotRecomm.png",
                    title:'ERP'
                  },{
                    imgSrc:"./static/img/hotRecomm.png",
                    title:''
                  }
                ],
                companyItems:[
                  {
                    imgSrc:"./static/img/hotRecomm.png",
                    title:''
                  },{
                    imgSrc:"./static/img/hotRecomm.png",
                    title:''
                  },{
                    imgSrc:"./static/img/hotRecomm.png",
                    title:''
                  }
                ],
                serviceItems:[
                  {
                    imgSrc:"./static/img/hotRecomm.png",
                    title:'CRM'
                  },{
                    imgSrc:"./static/img/hotRecomm.png",
                    title:''
                  },{
                    imgSrc:"./static/img/hotRecomm.png",
                    title:''
                  },{
                    imgSrc:"./static/img/hotRecomm.png",
                    title:'CRM'
                  },
                  {
                    imgSrc:"./static/img/hotRecomm.png",
                    title:''
                  },{
                    imgSrc:"./static/img/hotRecomm.png",
                    title:''
                  }
                ],
                obj:{
                  slider0:"./static/img/slider1.png",
                  slider1:"./static/img/slider2.jpg",
                  slider2:"./static/img/slider3.jpg"
                }
            }
        },
        mounted(){
          this.$refs.hot.style.width=(this.hotItems.length)*6+"rem";
          // console.log(this.$refs.company.style.width);
          this.$refs.company.style.width=(this.companyItems.length)*6+"rem";
          this.$refs.service.style.width=(this.serviceItems.length)*6+"rem";
        },
        components:{WarpSlider},
        methods:{
          jump:function(url,title,imgSrc){
            router.push({
              path:url,
              query:{
                topImgTrue:imgSrc,
                title:title,
                appid:title
              }
            });
          }
        }

    }
</script>
<style lang='less' scoped>
  @import '../../public/comment.less';
  .application{
    >p{
      text-align: center;
      font-size: 20px;
      padding:.5rem;
      cursor: pointer;
    }
    >div>{
      >h4{
        padding:.5rem;
        font-size: 20px;
        text-align: left;
        padding-left: .5rem;
        >span{
          float:right;
          color:@gray;
          font-size:12px;
          cursor:pointer;
        }
      }
      >div{
        height: 5.8rem;
        overflow: hidden;
        border-bottom: 1px @gray solid;
        >div{
          position: relative;
          height: 120%;
          overflow-x:auto;
          >ul{
            overflow: hidden;
            position: absolute;
            left:0;top:0;
            z-index: 1;
            // width: 30rem;
            >li{
              float: left;
              text-align: center;
              margin:1rem;
              >img{
                width: 3rem;
                height: 3rem;
                border-radius: .5rem;
              }
              >p{
                padding: .5rem;
                font-size: 12px;
              }
            }
          }
        }
      }
    }

  }
</style>



