<template>
  <header>
    <i class="icon-angle-left" @click="goBack"></i>
     <i v-if="closeSee" @click="closeWindow">x</i>
     <span>{{renderTitle}}</span>
     <slot></slot>
   </header>
</template>

<script>
import router from '../../router'
import {mapState,mapMutations} from 'vuex'
import { MessageBox} from 'mint-ui'
export default {
  name:'headerX',
  props:['headerObj'],
  data:function(){
    return {
      title:'',
      count:0,
      closeSee:false,
      path:this.renderPath
    }
  },
  computed:{
    renderTitle:function(){
      this.path=this.headerObj.path;
      return this.title=this.headerObj.title
    },
    ...mapState(["contactsSelected"])
  },
  methods:{
    ...mapMutations(["initSelectedContacts"]),
    goBack:function(){
      console.log("path",this.path);
      if(this.path=="/groupChat"&this.contactsSelected.length>1){
        // this.$emit("isGoBack")
        MessageBox.confirm('?').then(action => {
          console.log(action);
          router.go(-1);
          this.initSelectedContacts()
        },cancal=>{
          console.log(cancal);
        })
      }else if(this.path && this.path!="/groupChat"){
        console.log("patssh",typeof this.path);
        router.push({path:this.path})
      }else{
        //
        console.log("-1");
        if(this.count>=2){this.closeSee=true;}
        this.count++;
        router.go(-1);
      }

    },
    closeWindow:function(){
      router.replace({ path:this.path});
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../public/comment.less';;
  header{
    position: fixed;
    z-index: 100;
    left:0;top:0;
    height: 2rem;
    // line-height: 2rem;
    width: 100%;
    border-bottom: 1px #ccc solid;
    font-size: 20px;
    background: #eee;
    >i{
      color:#26a2ff;
      font-style: normal;
      height: 2rem;
      line-height: 2rem;
      padding:0 .5rem;
      vertical-align: middle;
      font-size: 26px;
    }
    >span{
      border-left: 1px @gray solid;
      padding-left:.4rem;
      display: inline-block;
      max-width: 180px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      vertical-align: middle;
    }
    >a{
      float: right;
      margin-right: .5rem;
      height: 2rem;
      line-height: 2rem;
      font-size: 16px;
      color:#26a2ff;
    }
  }
</style>



